import React from 'react'
import Card from './Card'
import img1 from '../img/html.png'
import img2 from '../img/css.png'
import img3 from '../img/javascript.png'
import img4 from '../img/bootstrap.png'
import img5 from '../img/nodejs.png'
import img6 from '../img/react.png'




function Cards(props) {
  const {idioma}=props
  const cards=[
  {
    id:1,
    title:'HTML',
    image:img1,
    url:function(){
      switch (idioma) {
        case 'Español':
          return 'https://developer.mozilla.org/es/docs/Web/HTML'
       case 'English':
          return 'https://developer.mozilla.org/en/docs/Web/HTML'
          default:
            break;
        }
      
      
    },
    text:function(){
      switch(idioma){
        case 'Español':
        return 'Html se refiere a las siglas en inglés HyperText Markup Language, hace referencia al lenguaje de marcado para la elaboración de páginas web, con html se crea la estructura de la pagina.'
        case 'English':
          return "HTML (HyperText Markup Language) is the most basic building block of the Web. It defines the meaning and structure of web content the use of html is limited to that, to add style you need to use CSS."
        default:
        break
      }
    }
  },
  {
    id:2,
    title:'CSS',
    image:img2,
    url:function(){
      switch(idioma){
        case 'Español':
        return 'https://developer.mozilla.org/es/docs/Web/CSS'
        case 'English':
        return 'https://developer.mozilla.org/en/docs/Web/CSS'
      default:
        break}},
        text:function(){
      switch(idioma){
        case 'Español':
        return 'CSS viene del inglés Cascading Style Sheets, es un lenguaje de estilos utilizado para describir la presentación de un documento html o xml, con CSS se agrega estilo y personalidad a una página web.'
        case 'English':
          return 'CSS is a stylesheet language used to describe the presentation of a document written in HTML or XML. CSS describes how elements should be rendered on screen, on paper, in speech, or on other media.'
        default:
          break
      }
    }


  },
  {
    id:3,
    title:'Javascript',
    image:img3,
    url:function(){
      switch (idioma) {
        case 'Español':
          return 'https://developer.mozilla.org/es/docs/Web/JavaScript'
        case 'English':
        return 'https://developer.mozilla.org/en/docs/Web/JavaScript'
        default:
          break;
      }
    },
      
    text:function(){
      switch(idioma){
        case 'Español':
        return  'JavaScript es un lenguaje de programación ligero, interpretado, o compilado justo-a-tiempo, JavaScript está basado en prototipos, con soporte para programación orientada a objetos y programación funcional.'
        case 'English':
        return 'JavaScript (JS) is a lightweight, interpreted, or just-in-time compiled programming language with first-class functions. JavaScript is a prototype-based, supporting object-oriented, imperative, and declarative.'
        default:
          break
      }
     }
  },
  {
    id:4,
    title:'Bootstrap',
    image:img4,
    url:function(){
      return 'https://getbootstrap.com/'
    },
    text:function(){switch(idioma){
      case 'Español':
        return 'Es una biblioteca(framework) multiplataforma de código abierto para el diseño de páginas web, bootstrap cuenta con estilos prediseñados para rápidamente darle estilo a tu sitio web.'
        case 'English':
          return 'It is an open-source CSS framework directed at responsive, mobile-first front-end web development. It contains CSS- and (optionally) JavaScript-based design templates to style your website.'
      
        default:
          break
    }}
  },
  {
    id:5,
    title:'Nodejs',
    image:img5,
    url:function(){
      switch (idioma) {
        case 'Español':
          return 'https://nodejs.org/es/'
       case 'English':
         return 'https://nodejs.org/en/'
      
        default:
          break;
      }
    },
    text:function(){
      switch(idioma){
         case 'Español':
           return 'Node.js es un entorno en tiempo de ejecución multiplataforma, basado en el lenguaje de programación JavaScript, con nodejs se puede programar el backend de una aplicación web.'
              case 'English':
             return 'Node.js is an open-source, cross-platform, back-end JavaScript runtime environment that runs on the V8 engine and executes JavaScript code outside a web browser. Ideal for developers to start learning backend.' 
           default:
          
             break
      }
     }
  },
  {
    id:6,
    title:'React',
    image:img6,
    url:function(){
      switch (idioma) {
        case 'Español':
          return 'https://es.reactjs.org/'
        case 'English':
          return 'https://en.reactjs.org/'
         default:
            break;
        }
      },
      text:function(){
        switch (idioma) {
          case 'Español':
            return 'React es una biblioteca JavaScript de código abierto diseñada para crear interfaces de usuario con el objetivo de facilitar el desarrollo de aplicaciones en una sola página.'
         case 'English':
           return 'React is an open-source, front end, JavaScript library for building user interfaces or UI components. It is maintained by Facebook and a community of individual developers and companies.'
        
          default:
            break;
        }
      },
    },
  ]
  return (
 <>
     {idioma==='Español'&&<h3 className="text-center p-4 h1 display-5" id='tecnologias'>Tecnologías que uso</h3>}
     {idioma==='English'&&<h3 className="text-center p-4 h1 display-5" id='tecnologias'>Technologies that I use</h3>}
        <div className="container d-flex justify-content-center align-items-center h-100">
      
      <div className="row">
        {
          cards.map(card=>
            <div className="col-md-4" key={card.id}>
          <Card title={card.title} imageSrc={card.image} url={card.url()} text={card.text()} idioma={idioma}/>
        </div>)
        }
      </div>
    </div>
    </>
  )
}
       
  
      
        
          
      


export default Cards