import React from "react";
import imagen1 from "../img/cube.png";
import imagen2 from "../img/camera.png";
import imagen3 from "../img/translation.png";
import Card2 from "./Card2";

const Proyectos = (props) => {
  const { idioma } = props;
  const cards = [
    {
      id: 1,
      title: "Cronometro para cubos de Rubik",
      image: imagen1,
      url: "https://github.com/ruben137/speedtimer",
      text: function () {
        switch (idioma) {
          case "Español":
            return "Uno de mis pasatiempos es armar cubos de rubik, así que, con HTML, CSS y JavaScript vanilla cree un cronometro para medir el tiempo";
          case "English":
            return "One of my hobbies is to solve Rubik's cubes, so with HTML, CSS and vanilla JavaScript I created a timer to measure time";
          default:
            break;
        }
      },

      path: "https://ruben137.github.io/speedtimer/",
    },
    {
      id: 2,
      title: "Sitio web de fotografía",
      image: imagen2,
      url: "https://github.com/ruben137/fotografia",
      text: function () {
        switch (idioma) {
          case "Español":
            return "Con HTML, CSS, bootstrap y JavaScript cree este sitio simple de fotografía, ideal para exponer tus trabajos y posicionar tu negocio en línea";
          case "English":
            return "With HTML, CSS, bootstrap and JavaScript I created this simple photography website, ideal to show your works and position your business online";

          default:
            break;
        }
      },
      path: "https://ruben137.github.io/fotografia/",
      
    },
    {
      id: 3,
      title: "Traducir tu pagina web con react",
      image: imagen3,
      url: "https://github.com/ruben137/webtranslation",
      text: function () {
        switch (idioma) {
          case "Español":
            return "En este pequeño proyecto con ayuda de React desarrollé una manera de agregar múltiples idiomas a una página web de manera práctica y sencilla";
          case "English":
            return "In this small project with help of React I developed a way of adding multiples languages to a website in a practical and simple way";

          default:
            break;
        }
      },
      path: "https://ruben137.github.io/webtranslation/",
      
    },
  ];
  return (
    <div className="container">
      {idioma==='Español'&&<h3 className="h1 text-center display-5" id="proyectos">
        Mis proyectos
      </h3>}
      {idioma==='English'&&<h3 className="h1 text-center display-5" id="proyectos">
        My projects
      </h3>}
      <div className="row text-center justify-content-center gap-4">
        {cards.map((card) => (
          <div
            className="card justify-content-center align-items-center"
            style={{ width: "22.25rem", height: "26rem" }}
            key={card.id}
          >
            <Card2
              title={card.title}
              imageSrc={card.image}
              url={card.url}
              text={card.text()}
              path={card.path}
              idioma={idioma}
            />
          </div>
        ))}
      </div>
    </div>
  );
};

export default Proyectos;
