import React from 'react'
import sololearn from '../img/sololearn.jpg'
import crehana from '../img/crehana.JPG'
import udemy from '../img/udemy.JPG'
const CertificadosMobile = () => {
  return (
    <>
      <div className="row d-lg-none">
        <div className="col-lg-6">
          <img src={sololearn} alt="" className='w-100 p-4'/>
        </div>
        <div className="col-lg-6">
          <img src={crehana} alt="" className='w-100 p-4'/>
        </div>
        <div className="col-lg-6">
          <img src={udemy} alt="" className='w-100 p-4'/>
        </div>
      </div>
    </>
  )
}

export default CertificadosMobile
