import React from "react";
import imagen from "../img/yo.png";

export default function About(props) {
  const { idioma } = props;
  return (
    <div className="contenedor" id="about">
      <div className="container text-center">
        <div className="row align-items-center">
          <div className="col-md-6 text-start mt-5">
            {idioma === "Español" && <h3 className="h1 display-6">Sobre mi</h3>}
            {idioma === "English" && <h3 className="h1 display-6">About me</h3>}
            {idioma === "Español" && (
              <p className="text-start">
                Mi nombre es Rubén González, tengo 21 años, soy de Venezuela y
                soy un apasionado del desarrollo web, recientemente me propuse
                la meta de aprender a programar y desde ese entonces cada día he
                dado lo mejor de mí para conseguir mis objetivos. Empece
                aprendiendo lo basico de html,css y JavaScript, pero a dia de
                hoy ya me he enfocado en aprender nuevas tecnologías tales como
                reactjs y nodejs.
              </p>
            )}
            {idioma === "English" && (
              <p className="text-start">
                My name is Rubén González, I'm 21 years old, I'm from Venezuela
                and I'm passionate about web development, I recently set myself
                the goal of learning to code and since then I have been everyday
                practicing and giving my best. I started learning the basics,
                html, css and JavaScript, but nowadays I have focused to learn
                other technologies such as reactjs and nodejs.
              </p>
            )}
            {idioma === "Español" && (
              <a className="btn btn-secondary" href="#contacto">
                Contáctame
              </a>
            )}
            {idioma === "English" && (
              <a className="btn btn-secondary" href="#contacto">
                Contact me
              </a>
            )}
          </div>

          <div className="col-md-6">
            <img className="imagen" src={imagen} alt="" />
          </div>
        </div>
      </div>
    </div>
  );
}
