import React from "react";

import { useForm } from "react-hook-form";
import emailjs from "emailjs-com";

const Footer = (props) => {
  const { idioma } = props;
  const { register, errors, handleSubmit } = useForm();
  const cambio = {
    nombre: function () {
      switch (idioma) {
        case "Español":
          return "Ingrese su nombre o el de su empresa";
        case "English":
          return "Enter your name or enterprise";
        default:
          break;
      }
    },
    email: function () {
      switch (idioma) {
        case "Español":
          return "Escribe tu correo";
        case "English":
          return "Write your email";
        default:
          break;
      }
    },
    subject: function () {
      switch (idioma) {
        case "Español":
          return "Escribe el asunto";
        case "English":
          return "Write the subject";

        default:
          break;
      }
    },
    message: function () {
      switch (idioma) {
        case "Español":
          return "Escribe tu mensaje";
        case "English":
          return "Write your message";

        default:
          break;
      }
    },
  };

  function onSubmit(data, e) {
    console.log(data);
    emailjs
      .sendForm(
        "service_yjpyokb",
        "template_u66luy4",
        e.target,
        "user_BG0ypmsCxzCHleU5wsrfk"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  }

  return (
    <footer id="contacto">
      <div className="bg-dark p-3 position-relative">
        <div className="col-md-3 m-auto card bg-light p-3 formulario">
          {idioma === "Español" && <h3 className="text-center">Contactame</h3>}
          {idioma === "English" && <h3 className="text-center">Contact me</h3>}
          <form className="form-group" onSubmit={handleSubmit(onSubmit)}>
            <input
              className="form-control my-2"
              placeholder={cambio.nombre()}
              name="nombre"
              ref={register({
                required: {
                  value: true,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "Campo requerido";
                      case "English":
                        return "Required field";

                      default:
                        break;
                    }
                  },
                },
                maxLength: {
                  value: 20,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "No mas de 20 caracteres";
                      case "English":
                        return "No more than 20 characters";

                      default:
                        break;
                    }
                  },
                },
                minLength: {
                  value: 1,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "Minimo 1 carácter";
                      case "English":
                        return "At least 1 character";

                      default:
                        break;
                    }
                  },
                },
              })}
            />

            <span className="text-danger small d-block mb-2">
              {errors?.nombre?.message()}
            </span>
            <input
              className="form-control my-2"
              placeholder={cambio.email()}
              name="email"
              ref={register({
                required: {
                  value: true,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "Campo requerido";
                      case "English":
                        return "Required field";

                      default:
                        break;
                    }
                  },
                },
                maxLength: {
                  value: 40,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "No mas de 40 caracteres";
                      case "English":
                        return "No more than 40 characters";
                      default:
                        break;
                    }
                  },
                },
              })}
            />
            <span className="text-danger small d-block mb-2">
              {errors?.email?.message()}
            </span>

            <input
              className="form-control my-2"
              placeholder={cambio.subject()}
              name="subject"
              ref={register({
                required: {
                  value: true,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "Campo requerido";
                      case "English":
                        return "Required field";

                      default:
                        break;
                    }
                  },
                },
                maxLength: {
                  value: 20,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "No mas de 20 caracteres";
                      case "English":
                        return "No more than 20 characters";

                      default:
                        break;
                    }
                  },
                },
              })}
            />
            <span className="text-danger small d-block mb-2">
              {errors?.subject?.message()}
            </span>

            <textarea
              name="texto"
              className="form-control"
              placeholder={cambio.message()}
              ref={register({
                required: {
                  value: true,
                  message: function () {
                    switch (idioma) {
                      case "Español":
                        return "Campo requerido";
                      case "English":
                        return "Required field";

                      default:
                        break;
                    }
                  },
                },
              })}
            ></textarea>
            <span className="text-danger small d-block mb-2">
              {errors?.texto?.message()}
            </span>
            <div className="d-grid">
              {idioma === "Español" && (
                <button className="btn btn-primary mt-2">Enviar</button>
              )}
              {idioma === "English" && (
                <button className="btn btn-primary mt-2">Send</button>
              )}
            </div>
          </form>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
