import React from "react";
import PropTypes from "prop-types";
import "./Cards.css";

function Card({ title, imageSrc, text, url, idioma }) {
  return (
    <div className="card text-center bg-dark animate__animated animate__fadeInUp mb-4">
      <div className="overflow">
        <img src={imageSrc} alt="" className="card-img-top" />
      </div>
      <div className="card-body text-light">
        <h4 className="card-title">{title}</h4>
        <p className="card-text text-secondary">
          {text
            ? text
            : "Lorem ipsum dolor sit amet consectetur adipisicing elit. Rem, repellat! Veniam, rerum maiores. Adipisci odit nobis, assumenda quibusdam, accusantium dignissimos dolores suscipit quisquam quod omnis cumque aperiam ullam non excepturi."}
        </p>
        {idioma === "Español" && (
          <a
            href={url}
            className="btn btn-outline-secondary rounded-0"
            target="_blank"
            rel="noreferrer"
          >
            Mas informacion
          </a>
        )}
        {idioma === "English" && (
          <a
            href={url}
            className="btn btn-outline-secondary rounded-0"
            target="_blank"
            rel="noreferrer"
          >
            More info
          </a>
        )}
      </div>
    </div>
  );
}

Card.propTypes = {
  title: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
  imageSrc: PropTypes.string,
  text: PropTypes.string,
};

export default Card;
