import React from 'react'
import PropTypes from 'prop-types'
import './Cards.css'


function Card2({ title, imageSrc, text, url, path, idioma }) {
  return (
    <>
      <a href={path} target='_blank' rel="noreferrer">
        <img style={{ width: '8rem' }} src={imageSrc} className="card-img-top p-2" alt="..." /><br />
      </a>
      <span className='h6' >Click en el icono</span>
      <div className="card-body">
        <a className="card-title h5 text-dark text-decoration-none">{title}</a>
        <p className="card-text">{text}</p>

        {idioma==='Español'&&<a href={url} className="btn btn-outline-secondary" target='_blank' rel="noreferrer">Ver en github</a>}
        {idioma==='English'&&<a href={url} className="btn btn-outline-secondary" target='_blank' rel="noreferrer">View on github</a>}
      </div>
    </>
  )
}
Card2.propTypes = {
  title: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
  imageSrc: PropTypes.string,
  text: PropTypes.string
}

export default Card2

            

 


