import React, { useState } from "react";

export default function Portada(props) {
  const { idioma } = props;
  const [color, setColor] = useState(null);

  const armar = () => {
    setColor("#26de81");
  };

  const colores = [1, 2, 3, 4, 5, 6, 7, 8, 9];
  return (
    <div className="container">
      <div className="row r-container d-flex align-items-center">
        <div className="col-md-6">
          {idioma === "Español" && (
            <h1 className="texto h2">
              Mi nombre es Rubén González, y soy Desarrollador Web
            </h1>
          )}
          {idioma === "English" && (
            <h1 className="texto h2">
              My name is Rubén González, and I am web developer
            </h1>
          )}

          {idioma === "Español" && (
            <p>
              A continuación se mostraran mis habilidades y proyectos realizados
            </p>
          )}
          {idioma === "English" && (
            <p>I will show you my skills and projects I have done below</p>
          )}
          {idioma === "Español" && (
            <a className="btn btn-primary me-2" href="#about">
              Sobre mí
            </a>
          )}
          {idioma === "English" && (
            <a className="btn btn-primary me-2" href="#about">
              About me
            </a>
          )}
        </div>
        <div className="cubo col-md-6 justify-content-center" id="cubo">
          {colores.map((el) => (
            <div
              className="colores"
              onMouseOver={() => setColor("#26de81")}
              onMouseOut={() => setColor("")}
              style={{ background: color, transition: "all .4s ease" }}
            ></div>
          ))}
        </div>
      </div>
    </div>
  );
}
